' use client'
import React, { useEffect, useState } from "react"
import axios from "axios"
import { Loader2 } from "lucide-react"

export default async function Page({ params }) {
    const { userid } = await params
    const [user, setUser] = useState(null)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        const fetchUser = async () => {
            try {
                const res = await axios.get(`http://localhost:5000/postuserprofile`, { id: userid })
                setUser(res.data)
            } catch (err) {
                console.error("Error fetching user", err)
            } finally {
                setLoading(false)
            }
        }

        fetchUser()
    }, [userid])

    if (loading) {
        return (
            <div className="flex justify-center items-center h-screen">
                <Loader2 className="animate-spin w-10 h-10 text-blue-500" />
            </div>
        )
    }

    if (!user) {
        return <div className="text-center mt-10">User not found</div>
    }

    return (
        <div className="max-w-2xl mx-auto p-4">
            {/* Profile Header */}
            <div className="flex items-center gap-6 mb-8">
                <img
                    src={user.avatar || "/default-avatar.png"}
                    alt={user.username}
                    className="w-24 h-24 rounded-full border-2 border-gray-300"
                />
                <div>
                    <h1 className="text-2xl font-bold">{user.username}</h1>
                    <p className="text-gray-600">{user.bio || "No bio yet"}</p>
                    <div className="flex gap-6 mt-3 text-sm text-gray-700">
                        <span><b>{user.posts?.length || 0}</b> Posts</span>
                        <span><b>{user.followers?.length || 0}</b> Followers</span>
                        <span><b>{user.following?.length || 0}</b> Following</span>
                    </div>
                    <button className="mt-4 px-4 py-2 bg-blue-500 text-white rounded-lg shadow hover:bg-blue-600 transition">
                        Follow
                    </button>
                </div>
            </div>

            {/* Posts Grid */}
            <div className="grid grid-cols-3 gap-3">
                {user.posts?.map((post) => (
                    <div key={post._id} className="relative aspect-square bg-gray-200 rounded-lg overflow-hidden">
                        <img
                            src={post.image}
                            alt="post"
                            className="w-full h-full object-cover hover:scale-105 transition-transform"
                        />
                    </div>
                ))}
            </div>
        </div>
    )
}
